"use client"

import { createContext, useContext, useState, useEffect, type ReactNode } from "react"
import { mockUsers } from "./mock-data"

type User = {
  id: string
  nome: string
  sobrenome: string
  email: string
  celular: string
  role: "admin" | "user"
  addresses?: {
    id: string
    label: string
    firstName: string
    lastName: string
    address: string
    city: string
    state: string
    zipCode: string
    phone: string
  }[]
}

type AuthContextType = {
  user: User | null
  isLoading: boolean
  login: (emailOrPhone: string, senha: string) => Promise<{ success: boolean; error?: string }>
  logout: () => void
  isAdmin: boolean
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Verificar sessão salva
    const savedUser = localStorage.getItem("memoriza_user")
    if (savedUser) {
      setUser(JSON.parse(savedUser))
    }
    setIsLoading(false)
  }, [])

  const login = async (emailOrPhone: string, senha: string) => {
    // Simular delay da API
    await new Promise((resolve) => setTimeout(resolve, 500))

    const foundUser = mockUsers.find(
      (u) => (u.email === emailOrPhone || u.celular === emailOrPhone) && u.senha === senha,
    )

    if (foundUser) {
      const userData: User = {
        id: foundUser.id,
        nome: foundUser.nome,
        sobrenome: foundUser.sobrenome,
        email: foundUser.email,
        celular: foundUser.celular,
        role: foundUser.role as "admin" | "user",
        addresses: foundUser.addresses,
      }
      setUser(userData)
      localStorage.setItem("memoriza_user", JSON.stringify(userData))
      return { success: true }
    }

    return { success: false, error: "E-mail/celular ou senha incorretos" }
  }

  const logout = () => {
    setUser(null)
    localStorage.removeItem("memoriza_user")
  }

  return (
    <AuthContext.Provider value={{ user, isLoading, login, logout, isAdmin: user?.role === "admin" }}>
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
